.recipes-css-transition {
    position: 'fixed';
    top: 0;
    z-index: -3000;
    width: '100%';
}
  
.css-transition-enter {
    -webkit-transform: translate(100%);
            transform: translate(100%);
}
.css-transition-enter.css-transition-enter-active {
    -webkit-transform: translate(0%);
            transform: translate(0%);
    -webkit-transition: -webkit-transform 4000ms ease-in-out;
    transition: -webkit-transform 4000ms ease-in-out;
    transition: transform 4000ms ease-in-out;
    transition: transform 4000ms ease-in-out, -webkit-transform 4000ms ease-in-out;
}
.css-transition-leave {
    -webkit-transform: translate(0%);
            transform: translate(0%);
}
.css-transition-leave.css-transition-leave-active {
    -webkit-transform: translate(-100%);
            transform: translate(-100%);
    -webkit-transition: -webkit-transform 4000ms ease-in-out;
    transition: -webkit-transform 4000ms ease-in-out;
    transition: transform 4000ms ease-in-out;
    transition: transform 4000ms ease-in-out, -webkit-transform 4000ms ease-in-out;
}
  
